.block__breadcrumb {
  background-color: white;
  padding: 0.7rem 20px 0.5rem;
  margin: 1.5rem 0;
  border-radius: 0.4rem;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  .breadcrumb {
    &__container {
      max-width: 1200px;
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      height: auto;
    }

    &__item {
      position: relative;
      display: inline-flex;
      align-items: center;
      padding: 0.3rem 0.4rem;
      padding-left: 0;
      color: #1a237e;
      font-weight: 600;
      font-size: 1.4rem !important;
      text-decoration: none;
      line-height: 1.2;

      &:first-child {
        padding-left: 0;
        margin-left: 0;
      }

      &--home {
        color: #304ffe;
        padding-left: 0;
        margin-left: 0;
      }

      &--active {
        color: #0d47a1;
        font-weight: 700;
      }

      a {
        color: inherit;
        text-decoration: none;
        font-size: 1.4rem !important;

        &:hover {
          color: #1976d2;
          text-decoration: underline;
        }
      }
    }

    &__separator {
      color: #5c6bc0;
      margin: 0 0.5rem;
      font-size: 1.8rem !important;
    }

    &__icon {
      margin-right: 0.5rem;
      font-size: 1.4rem !important;
    }

    @media (max-width: 768px) {
      padding: 0.3rem 0;

      &__item {
        font-size: 1.8rem;
      }

      &__separator {
        font-size: 1.5rem;
      }

      &__icon {
        font-size: 1.4rem;
      }
    }

    @media (max-width: 480px) {
      &__item {
        font-size: 1.3rem;
        padding: 0.2rem 0.2rem;
      }

      &__separator {
        font-size: 1.1rem;
        margin: 0 0.2rem;
      }

      &__icon {
        font-size: 1.2rem;
        margin-right: 0.2rem;
      }
    }
  }
}

.block__breadcrumb,
.user__section,
.product-details {
  .breadcrumb {
    margin-left: 0;
    padding-left: 0;

    &__container {
      padding-left: 0;
      margin-left: 0;
    }

    &__item {
      &:first-child {
        padding-left: 0;
        margin-left: 0;
      }

      &--home {
        padding-left: 0;
        margin-left: 0;
      }
    }

    &__item {
      font-size: 1.8rem;

      a {
        font-size: 1.8rem;
      }
    }

    &__separator {
      font-size: 1.5rem;
    }

    &__icon {
      font-size: 1.4rem;
    }
  }
}
