@import 'variables';
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
.text {
  font-family: $font-body;
  font-size: 1.5rem;
  font-style: italic;
  color: $color-dark-text;
  line-height: 1.4;
}
.text--light {
  font-weight: 300;
}
.text--normal {
  font-style: normal;
}
