.home {
  margin-top: 30px;
  &__banner {
    position: relative;
    .img-banner {
      position: relative;
      @include size(100%, 100%);
    }
    .banner {
      .img-banner {
        max-width: 100%; // Đảm bảo ảnh không rộng hơn container
        height: auto; // Giữ nguyên tỷ lệ ảnh
        object-fit: contain;
      }
      &__content {
        @include absoluteFull();
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: 80px;
        width: 600px;
        gap: 10px;

        .discount {
          color: #166534;
          font-size: 1.2rem;
          font-style: normal;
          font-weight: 600;
          line-height: 18px;

          width: fit-content;
          padding: 7px 10px;

          border-radius: 6px;
          background: linear-gradient(90deg, rgba(22, 163, 74, 0.5) 0%, rgba(34, 197, 94, 0) 60%);
        }

        .title {
          color: $color-primary-purple;
          font-size: 4.8rem;
          font-style: normal;
          font-weight: 700;
          line-height: 62.4px;
          width: 100%;
        }

        .describe {
          color: $color-dark-text;
          font-size: 1.6rem;
          font-style: normal;
          font-weight: 400;
          line-height: normal;
          width: calc(100% - 10px);
        }

        .btn-shop {
          @include flexbox(center, center, row, flex, nowrap);
          gap: 10px;
          width: fit-content;
          padding: 0 18px;
          p {
            color: #fff;
            text-align: center;
            font-size: 14px;
            font-style: normal;
            font-weight: 700;
            letter-spacing: 1px;
          }
        }
      }
    }
  }

  &__section {
    @include flexbox(flex-start, center, column, flex, nowrap);
    gap: 30px;
    width: 100%;
    margin: 30px auto 50px;
    .section__title {
      @include flexbox(center, space-between, row, flex, nowrap);
      width: 100%;
      .title {
        color: $color-dark-text;
        font-size: 2.8rem;
        font-style: normal;
        font-weight: 700;
        line-height: 28px;
        text-transform: capitalize;
      }
      .btn-viewAll {
        @include flexbox(center, center, row, flex, nowrap);
        align-self: center;
        gap: 5px;
        width: fit-content;
        background-color: white;
        border-radius: 999px;
        border: 1px solid #e5e7eb;
        padding: 10px 16px 10px 12px;

        &:hover {
          background-color: #e5e7eb;
        }

        p {
          color: $color-dark-text;
          text-align: center;
          font-size: 1.2rem;
          font-style: normal;
          font-weight: 700;
          line-height: 10px; /* 83.333% */
        }

        img {
          @include size(12px, 100%);
        }
      }
    }

    .section__content {
      width: 100%;
      &.section__home-cate {
        .category-swiper {
          width: 100%;
          padding-bottom: 30px; // Để chừa chỗ cho bullets

          .swiper-slide {
            height: auto;
            display: flex;
            justify-content: center;
          }

          .swiper-pagination {
            bottom: 0;
          }

          .item-categories {
            @include size(calc(10% + 20px), calc($container / 10 + 50px));
            border: 1px solid rgb(243, 243, 243);
            background-color: #ffffff;
            position: relative;
            width: 100%; // Đảm bảo rằng nó lấp đầy slide

            &:hover {
              border-color: rgba(0, 0, 0, 0.12);
              box-shadow: 0 0 0.8125rem 0 rgba(0, 0, 0, 0.05);
              transform: translateZ(0);
              z-index: 2;
            }

            .link-categories {
              @include flexbox(center, flex-start, column, flex, nowrap);
              @include size(100%, 100%);
              overflow: hidden;
              padding: 15px 15px 0 15px;
              position: relative;

              img.category-image {
                position: relative;
                width: 100%;
                height: 100%;
                max-height: 120px;
                object-fit: contain;
                margin: auto 0;
                margin-top: 10px;
              }

              span {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                color: $color-dark-text;
                font-size: 1.4rem;
                font-weight: 600;
                line-height: 1.5;
                text-align: center;
                padding: 0 15px;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                text-overflow: ellipsis;
                min-height: 50px;
              }
            }
          }
        }
      }
    }
  }
}
